<template>
    <v-ons-page>
        <v-ons-toolbar>
            <div class="center">Member</div>
            <div class="left">
                <ons-back-button @click="goBack">Back</ons-back-button>
            </div>
        </v-ons-toolbar>
        <form action="">
            <v-ons-list>
                <v-ons-list-header class="heading-title">Member</v-ons-list-header>
                <v-ons-list-item class="input-items">
                    <v-ons-input class="input-field" modifier="material" placeholder="Last Period" v-model="LastPeriod"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Target" v-model="Target"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Actual" v-model="Actual"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Increased" v-model="Increased"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Decreased" v-model="Decreased"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Member Contact" v-model="MemberContact"></v-ons-input>
                </v-ons-list-item>
                <v-ons-list-item>
                    <v-ons-input class="input-field" modifier="material" placeholder="Comment" v-model="Comment"></v-ons-input>
                </v-ons-list-item>

                <v-ons-button class="button-margin next-button" type="submit" @click="submitMember">
                    Save
                </v-ons-button>
            </v-ons-list>
        </form>
    </v-ons-page>
</template>

<script>
//import axios from 'axios'
import Member from './Member.vue';
import ReportLandingPage from './ReportLandingPage.vue';

export default {
    data() {
        return {
            LastPeriod: '',
            Target: '',
            Actual: '',
            Increased: '',
            Decreased: '',
            MemberContact: '',
            Comment: '',

            // member: {
            //     LastPeriod: '',
            //     Target: '',
            //     Actual: '',
            //     Increased: '',
            //     Decreased: '',
            //     MemberContact: '',
            //     Comment: ''
            // }
        }
    },
    mounted() {

    },
    methods: {
        pushPage() {
            this.$store.commit('navigator/push', Member);
        },
        goBack() {
            this.$store.commit('navigator/push', ReportLandingPage);
        },
        submitMember() {
            
            // axios
            // .post('https://localhost:44397/api/members', { 
            //     LastPeriod: this.LastPeriod,
            //     Target: this.Target,
            //     Actual: this.Actual,
            //     Increased: this.Increased,
            //     Decreased: this.Decreased,
            //     MemberContact: this.MemberContact,
            //     Comment: this.Comment
            //  })
            // .then(response => console.log(response))

            localStorage.LastPeriod = this.LastPeriod;
            localStorage.Target = this.Target;
            localStorage.Actual = this.Actual;
            localStorage.Increased = this.Increased;
            localStorage.Decreased = this.Decreased;
            localStorage.MemberContact = this.MemberContact;
            localStorage.Comment = this.Comment;

            this.LastPeriod = '';
            this.Target = '';
            this.Actual = '';
            this.Increased = '';
            this.Decreased = '';
            this.MemberContact = '';
            this.Comment = '';
        }
    }
}
</script>


