<template>
    <v-ons-page>
        <div class="center search">
            <v-ons-search-input id="search-input" placeholder="Search"></v-ons-search-input>
        </div>
        <v-ons-card class="card-radius plan-card">
            <div class="content">
                <p class="report-card" v-for="report in items" :key="report.uri" @click="pushPage">
                    {{ report }}
                </p>
            </div>
        </v-ons-card>
    </v-ons-page>
</template>

<script>
import ReportLandingPage from './ReportLandingPage.vue';

export default {
    data() {
        return {
            items: [
                { text: '2019 Jan-Apr', value: '2019 Jan-Apr' },
                { text: '2019 May-Aug', value: '2019 May-Aug' },
                { text: '2019 Sept-Dec', value: '2019 Sept-Dec' }
            ]
        }
    },
    methods: {
        pushPage() {
            this.$store.commit('navigator/push', ReportLandingPage);
        }
    }    
}
</script>


